.navbar-fixed {
    font-family: 'Raleway', sans-serif;
}

.brand-logo {
    height: 4rem;
    margin: 0 0 0 4rem;
}

.brand-logo > svg {
    fill: #36213E;
}

li {
    cursor: pointer;
}

.support {
    background-color: #36213e;
    color: #8ac6d0 !important;
    width: 12rem;
}

.support:hover {
    background-color: #36213e;
    opacity: 0.9 !important;
}

.nav-wrapper {
    background-color: #8ac6d0 !important;
}

.dropdown-trigger {
    width: 12rem;
}

#leadership-icon {
    margin-left: 0;
    margin-right: 1rem; 
}

.dropdown-content {
    background-color: #8ac6d0 !important;
}

#nav-text {
    color: #36213e;
}

#first-lander {
    height: 100vh;
    overflow: hidden;
}

figure.col {
    margin: 0;
    padding: 0 !important;
}

figure.col > img {
    height: 100%;
    max-height: 100%;
    max-width: 100%;
    padding: 0;
    width: 100%;
}

.about {
    margin: 5rem 20rem;
}

.about-title {
    font-family: 'Raleway', sans-serif;
    font-weight: bold;
    margin: 2rem 0;
}

.about-p {
    font-family: 'Lato', sans-serif !important;
    margin: 1rem 0 2rem 0 !important;
}

#mail-list-form {
    background-color: #f1f1f1;
    height: 46.95rem;
    padding: 6rem 0 0 0;
}

.title {
    font-family: 'Raleway', sans-serif;
}

.lbl-tinyltr {
    font-family: 'Raleway', sans-serif;
}

#email, #FIRSTNAME, #LASTNAME {
    background-color: #fff;
    border-bottom: none;
    border-right: 0.5rem solid #ff0000;
    text-indent: 1rem;
}

#ORGANIZATION {
    background-color: #fff;
    border-bottom: none;
    text-indent: 1rem;   
}

#email:focus, #FIRSTNAME:focus, #LASTNAME:focus, #ORGANIZATION:focus {
    box-shadow: 0 1px 0 0 #8ac6d0 !important;
}

.waves-effect {
    background-color: #36213e !important;
    color: #8ac6d0 !important;
    font-family: 'Lato', sans-serif;
}

.title {
    font-family: 'Raleway', sans-serif;
}

#mail-form-p {
    font-family: 'Lato', sans-serif !important;
    font-size: 1.1rem;
    margin: 0;
}

#staff {
    background-color: #f1f1f1;
    margin: 0;
    padding: 2rem 0;
}

.staff-title {
    font-family: 'Raleway', sans-serif !important;
    margin: 0;
    padding: 2rem 0 1rem 0;
}

.staff-grid-p {
    font-family: 'Lato', sans-serif !important;
    margin: 0 0 1rem 0;
    padding: 0 20rem;
}

#staff-card {
    cursor: pointer;
    color: #36213e;
    margin: 1rem 0 ;
}

#staff-card:hover {
    color: #8ac6d0 !important;
}

.card-content.center {
    -webkit-transition: color 0.25s ease;
    transition: color 0.25s ease;
}

.staff-card-name {
    font-family: 'Raleway', sans-serif !important;
    margin: 0;
}

.staff-card-title {
    color: #A9A9A9;
    font-family: 'Lato', sans-serif !important;
    font-size: 0.9em;
    margin: 0.1em 0 0 0;
}

.page-footer {
    margin: 0 !important;
}

.footer {
    padding: 0 0 2rem 0;
}

.twitter {
    padding: 2rem 0 0 2rem;
}

.twitter > svg {
    fill: #1da1f2;
}

.footer-title {
    font-family: 'Raleway', sans-serif !important;
    padding: 1rem 0 0 2rem;
}

.footer-mail > a {
    color: #A9A9A9;
    font-family: 'Lato', sans-serif !important;
    padding: 0 0 0 2rem;
}

