.board-info {
    background-color: #f1f1f1;
    padding: 2rem 0 0 0;
}

#board-row {
    margin: 0;
}

.board-title {
    font-family: 'Raleway', sans-serif;
    margin: 0 0 2rem 0;
    padding: 2rem 0 0 4rem;
}

#board-grid-p {
    font-family: 'Raleway', sans-serif;
    margin: 0;
    padding: 0 0 4rem 4rem;
}

#board-card {
    color: #36213e;
    cursor: pointer;
    margin: 1rem 0;
}

#board-card:hover {
    color: #8ac6d0 !important;
}

.card-content.center {
    -webkit-transition: color 0.25s ease;
    transition: color 0.25s ease;
}

h2, h4, h5 {
    font-family: 'Raleway', sans-serif;
}

